<template>
  <div class="settlement-records">
    <h5 class="chart-title">结算列表</h5>

    <el-table
      :data="list"
      stripe
      size="small"
      v-loading="isLoading"
      style="width: 100%">
      <el-table-column label="序号" align="center" width="80px" type="index">
        <template slot-scope="scope">
          <span>{{ listIndex + scope.$index + 1 }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="stmtName" label="结算期数" align="center">
        <template slot-scope="scope">
          <el-link type="primary" @click="toDetail(scope.row.id)">{{ scope.row.stmtName }}</el-link>
        </template>
      </el-table-column>
      <el-table-column prop="stmtType" label="结算类型" align="center"></el-table-column>
      <el-table-column prop="stmtSts" label="结算状态" align="center">
        <template slot-scope="scope">
          <span :style="scope.row.stmtSts | fmtStmtStsColor">{{ scope.row.stmtSts | fmtStmtSts }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="stmtMan" label="收款确认人" align="center"></el-table-column>
      <el-table-column prop="stmtDate" label="收款确认时间" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.stmtDate | fmtDateTime }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="结算生成时间" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.createTime | fmtDateTime }}</span>
        </template>
      </el-table-column>
    </el-table>

    <el-new-pagination :getData="getSettlementList" type="popup" ref="pagination"></el-new-pagination>
  </div>
</template>

<script>
import ElNewPagination from '@/components/ElNewPagination'

export default {
  name: 'settlement-overview',
  components: {
    ElNewPagination
  },
  props: {
    search: Object
  },
  data () {
    return {
      list: [],
      listIndex: 0,
      isLoading: false
    }
  },
  methods: {
    getSettlementList (handler) {
      this.isLoading = true
      handler('serviceGetStmtList', this.search)
        .then(res => {
          this.list = res.list
          this.listIndex = (res.pageNum - 1) * res.pageSize
        })
        .finally(() => {
          this.isLoading = false
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.settlement-overview {
  .chart-title {
    display: flex;
    align-items: center;
    margin: 20px 0;
    font-size: 13px;
    line-height: 1.2;
    .el-link {
      margin-left: 3px;
    }
  }

  ::v-deep .white-bg {
    background-color: #fff;
  }

  ::v-deep .strong-number:not(:first-child) {
    font-size: 16px!important;
  }
}
</style>
