<template>
  <div class="chart-container">
    <h5 class="chart-title">
      <div>
        结算状态统计-应收
        <el-popover :content="$slots.default ? $slots.default[0].text : '统计在当前筛选条件下所有由本账户发行的积分，已核销应收部分在各结算状态下的分布情况'">
          <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
        </el-popover>
      </div>
      <el-select v-model="filter" size="small">
        <el-option label="全部" value=""></el-option>
        <el-option :label="d.name" :value="d.id" v-for="d in deptList" :key="d.id"></el-option>
      </el-select>
    </h5>

    <div class="chart-main">
      <div class="chart">
        <div class="total"><span>{{ total }}</span>总计</div>
        <v-chart :options="options"></v-chart>
      </div>
      <div class="table">
        <el-table
          :data="datas"
          size="small"
          show-summary
          :summary-method="getSummaries"
          style="width: 100%">
          <el-table-column prop="name" label="">
            <template slot-scope="scope">
              <span class="colorTag"><i></i>{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="integral" label="积分">
            <template slot-scope="scope">{{ scope.row.integral | fmtMoney }}</template>
          </el-table-column>
          <el-table-column prop="proportion" label="积分占比">
            <template slot-scope="scope">{{ (scope.row.integral / total) | fmtFloat2Percent }}</template>
          </el-table-column>
          <el-table-column prop="fee" label="费用（元）">
            <template slot-scope="scope">{{ scope.row.fee | fmtMoney }}</template>
          </el-table-column>
          <el-table-column prop="feeProportion" label="费用占比">
            <template slot-scope="scope">{{ scope.row.feeProportion | fmtFloat2Percent }}</template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import pie from './common/pie'
import { fmtMoney, fmtFloat2Percent } from '@/filters'
import { mapGetters } from 'vuex'

export default {
  name: 'chart-settlement-status-receivable',
  mixins: [pie],
  data () {
    return {
      options: {
        tooltip: {
          formatter: (params) => {
            return `${params.name}<br/>积分：${fmtMoney(params.integral)}<br/>积分占比：${fmtFloat2Percent(params.proportion)}<br/>费用（元）：${fmtMoney(params.fee)}<br/>费用占比：${fmtFloat2Percent(params.feeProportion)}`
          }
        },
        series: [
          {
            name: '结算状态统计 - 应收',
            type: 'pie',
            radius: ['60%', '90%'],
            label: { show: false },
            labelLine: { show: false },
            data: []
          }
        ]
      },
      filter: ''
    }
  },
  computed: {
    ...mapGetters({
      deptList: 'getDeptList'
    })
  },
  methods: {
    isMatchCondition (column) {
      return column.property === 'integral' || column.property === 'fee'
    }
  }
}
</script>

