<template>
  <div class="chart-container">
    <h5 class="chart-title">
      参与次数环比统计
      <el-popover :content="$slots.default ? $slots.default[0].text : '统计本账户内，当前筛选条件下所有活动用户参与次数的环比与增长率'">
        <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
      </el-popover>
    </h5>

    <div class="chart-main">
      <div class="chart">
        <v-chart :options="options" ref="chart"></v-chart>
      </div>
      <el-row style="margin-top: 10px">
        <el-col :span="8">
          <el-table class="table color2" :data="datas.table[0]" size="mini" style="width: 100%">
            <el-table-column prop="totalTimes" label="总参与次数"></el-table-column>
            <el-table-column prop="timesDaily" label="日均参与次数"></el-table-column>
          </el-table>
        </el-col>
        <el-col :span="8" :offset="8">
          <el-table class="table color1" :data="datas.table[1]" size="mini" style="width: 100%">
            <el-table-column prop="growth" label="本期增长">
              <template slot-scope="scope">
                <span>{{ scope.row.growth | fmtFloat2Percent }}</span><arrow-icon :value="scope.row.growth" />
              </template>
            </el-table-column>
            <el-table-column prop="yearGrowth" label="同比增长">
              <template slot-scope="scope">
                <span>{{ scope.row.yearGrowth | fmtFloat2Percent }}</span><arrow-icon :value="scope.row.yearGrowth" />
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import bar from './common/bar'
import { fmtMoney, fmtFloat2Percent } from '@/filters'

export default {
  name: 'chart-participate-times-chain',
  mixins: [bar],
  data () {
    return {
      options: {
        tooltip: {
          formatter: (params) => {
            if (params.componentType !== 'series') return ''
            const series = this.datas.chart.find(c => c.name === params.seriesName)
            const data = series.data.find(d => d.section === params.name)
            if (series.name === '次数') {
              return `${params.name}<br/>次数：${data.userNum}<br/>日均人数：${data.userNumDaily}`
            } else {
              return `${params.name}<br/>增长率：${fmtFloat2Percent(data.ratio)}`
            }
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '次数',
            min: 0,
            splitLine: false,
            axisLabel: { formatter: value => fmtMoney(value) }
          },
          {
            type: 'value',
            name: '增长率（%）',
            min: 0,
            splitLine: false,
            axisLabel: { formatter: value => fmtFloat2Percent(value) }
          }
        ],
        series: [
          {
            type: 'line',
            yAxisIndex: 1,
            markPoint: {
              data: [
                { type: 'max', name: '最大值' }
              ],
              label: {
                formatter: params => fmtFloat2Percent(params.value)
              }
            }
          },
          {
            type: 'bar',
            markPoint: {
              data: [
                { type: 'max', name: '最大值' },
                { type: 'min', name: '最小值' }
              ]
            },
            markLine: {
              data: [
                { type: 'average', name: '平均值' }
              ]
            }
          }
        ]
      },
      chartValue: {
        增长率: 'ratio',
        用户数: 'times'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import './common/bar.scss';

.chart-container {
  .table {
    ::v-deep .el-table__header th { padding-bottom: 0; }
    ::v-deep .el-table__body td { font-size: 16px; }
  }

  .chart-main {
     .table.color1 ::v-deep .el-table__body td { border-bottom: 6px solid #c23531; }
     .table.color2 ::v-deep .el-table__body td { border-bottom: 6px solid #2f4554; }
     .table.color3 ::v-deep .el-table__body td { border-bottom: 6px solid #61a0a8; }
     .table.color4 ::v-deep .el-table__body td { border-bottom: 6px solid #d48265; }
  }
}
</style>
