<template>
  <div class="chart-container">
    <h5 class="chart-title">
      业务归属环比统计
      <el-popover :content="$slots.default ? $slots.default[0].text : '统计平台内，当前筛选条件下的已发行、未使用、已核销、已回收积分的业务归属环比情况'">
        <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
      </el-popover>
    </h5>

    <div class="chart-main">
      <el-row :gutter="20">
        <el-col :span="16">
          <div class="chart">
            <v-chart :options="options" ref="chart"></v-chart>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="table">
            <el-radio-group v-model="type" class="btns" size="small">
              <el-radio-button :label="0">已发行</el-radio-button>
              <el-radio-button :label="1">未使用</el-radio-button>
              <el-radio-button :label="2">已核销</el-radio-button>
              <el-radio-button :label="3">已回收</el-radio-button>
            </el-radio-group>
            <el-table
              :data="datas.table[type]"
              size="small"
              style="width: 100%">
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="integral" label="积分">
                <template slot-scope="scope">{{ scope.row.integral | fmtMoney }}</template>
              </el-table-column>
              <el-table-column prop="proportion" label="占比">
                <template slot-scope="scope">{{ scope.row.proportion | fmtFloat2Percent }}</template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import bar from './common/bar'
import { fmtMoney, fmtFloat2Percent } from '@/filters'

export default {
  name: 'user-bussiness-chain',
  mixins: [bar],
  data () {
    return {
      type: 0,
      options: {
        tooltip: {
          formatter: (params) => {
            if (params.componentType !== 'series') return ''
            const series = this.datas.chart.find(c => c.name === params.seriesName)
            const data = series.data.find(d => d.section === params.name)
            return `${series.name}<br/>积分：${fmtMoney(data.integral)}<br/>占比：${fmtFloat2Percent(data.proportion)}`
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '积分值',
            min: 0,
            splitLine: false,
            axisLabel: { formatter: value => fmtMoney(value) }
          },
          {
            type: 'value',
            name: '总计',
            min: 0,
            splitLine: false,
            axisLabel: { formatter: value => fmtMoney(value) }
          }
        ],
        series: [
          {
            type: 'bar',
            markPoint: {
              data: [
                { type: 'max', name: '最大值' },
                { type: 'min', name: '最小值' }
              ]
            },
            markLine: {
              data: [
                { type: 'average', name: '平均值' }
              ]
            }
          },
          {
            type: 'bar',
            markPoint: {
              data: [
                { type: 'max', name: '最大值' },
                { type: 'min', name: '最小值' }
              ]
            },
            markLine: {
              data: [
                { type: 'average', name: '平均值' }
              ]
            }
          },
          {
            type: 'line',
            yAxisIndex: 1,
            markPoint: {
              data: [
                { type: 'max', name: '最大值' }
              ]
            }
          }
        ]
      }
    }
  },
  methods: {
    isMatchCondition (column) {
      return column.property === 'integral'
    }
  }
}
</script>

<style lang="scss" scoped>
@import './common/bar.scss';
</style>
