import ECharts from 'vue-echarts'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/legend'
import { fmtMoney } from '@/filters'

export default {
  components: {
    'v-chart': ECharts
  },
  props: {
    /**
     * datas.chart 是折线图的数据
     * 例如 datas.chart = [
     *   {
     *     name: '未使用',
     *     data: [
     *       {
     *         section: '1月',
     *         integral: 1462,
     *         proportion: 0.35
     *       }
     *     ]
     *   }
     * ]
     * datas.table 是柱状图附带的统计数据
     */
    datas: {
      type: Object,
      default: () => ({ chart: [], table: [] })
    }
  },
  data () {
    return {
      options: { // 折线图通用配置
        tooltip: {
          backgroundColor: '#fff',
          borderColor: '#909399',
          borderWidth: 1,
          textStyle: {
            color: '#303133',
            fontSize: 13
          },
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          bottom: 0
        },
        xAxis: { type: 'category' },
        yAxis: [],
        dataset: { // 采用dataset方式渲染数据
          dimensions: ['sections'],
          source: []
        },
        series: []
      },
      chartValue: 'growth' // 可选值：[String, Object]
    }
  },
  watch: {
    datas: { // 数据的检测与图表更新
      handler: function (val) {
        const dimensions = val.chart.map(c => c.name)
        this.options.legend.data = dimensions
        this.options.dataset.dimensions = ['sections', ...dimensions]

        function getChartValue (chartValue, key) {
          if (typeof chartValue === 'string') return chartValue
          if (Object.prototype.toString.call(chartValue) === '[object Object]') {
            return chartValue[key]
          }
          return ''
        }
        const sections = val.chart[0] ? val.chart[0].data.map(d => d.section) : []
        this.options.dataset.source = sections.map(s => [s, ...dimensions.map(d => val.chart.find(c => c.name === d).data.find(dt => dt.section === s)[getChartValue(this.chartValue, d)])])
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    getSummaries (param) { // 表格统计的自定义方法
      const { columns, data } = param
      const sums = []

      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '总计'
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (this.isMatchCondition(column) && !values.every(value => isNaN(value))) {
          sums[index] = fmtMoney(values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0))
        } else {
          sums[index] = ''
        }
      })

      return sums
    },

    isMatchCondition (column) { // 判断是否需要总计的属性值，默认不判断
      return true
    }
  }
}
