<template>
  <div class="chart-container">
    <h5 class="chart-title">
      增长率
      <el-popover :content="$slots.default ? $slots.default[0].text : '统计本账户内，当前筛选条件下的已发行、未使用、已核销、已回收的增长率'">
        <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
      </el-popover>
    </h5>

    <div class="chart-main">
      <el-row :gutter="20">
        <el-col :span="16">
          <div class="chart">
            <v-chart :options="options" ref="chart"></v-chart>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="table">
            <el-table
              :data="datas.table[0]"
              size="small"
              style="width: 100%">
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="growth" label="本期增长">
                <template slot-scope="scope">{{ scope.row.growth | fmtFloat2Percent }}<arrow-icon :value="scope.row.growth" /></template>
              </el-table-column>
              <el-table-column prop="syncGrowth" label="同比增长">
                <template slot-scope="scope">{{ scope.row.syncGrowth | fmtFloat2Percent }}<arrow-icon :value="scope.row.syncGrowth" /></template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import line from './common/line'
import { fmtFloat2Percent } from '@/filters'

export default {
  name: 'chart-points-growth-rate',
  mixins: [line],
  data () {
    return {
      options: {
        tooltip: {
          formatter: (params) => {
            if (params.componentType !== 'series') return ''
            const series = this.datas.chart.find(c => c.name === params.seriesName)
            const data = series.data.find(d => d.section === params.name)
            return `${series.name}<br/>增长率：${fmtFloat2Percent(data.growth)}`
          }
        },
        yAxis: {
          type: 'value',
          name: '增长率（%）',
          splitLine: false,
          axisLabel: { formatter: (value, index) => fmtFloat2Percent(value) }
        },
        series: [
          {
            type: 'line',
            markPoint: {
              data: [
                { type: 'max', name: '最大值' }
              ],
              label: {
                formatter: params => fmtFloat2Percent(params.value)
              }
            }
          },
          {
            type: 'line',
            markPoint: {
              data: [
                { type: 'max', name: '最大值' }
              ],
              label: {
                formatter: params => fmtFloat2Percent(params.value)
              }
            }
          },
          {
            type: 'line',
            markPoint: {
              data: [
                { type: 'max', name: '最大值' }
              ],
              label: {
                formatter: params => fmtFloat2Percent(params.value)
              }
            }
          }
        ]
      }
    }
  }
}
</script>

