<template>
  <el-row :gutter="gutter">
    <el-col :span="6" v-for="(item, index) in data" :key="'today-data-' + index">
      <el-new-card>
        <template slot="header">{{ item.name }}</template>
        <div class="comparative-data">
          <strong>{{ item.value | fmtMoney }}</strong>
          <p>
            <span>{{ item.name | lastText }}：{{ item.lastVal | fmtMoney }}</span>
            <arrow-icon :value="item.value - item.lastVal">{{ Math.abs((item.value - item.lastVal) / item.lastVal) | fmtFloat2Percent }}</arrow-icon>
          </p>
        </div>
      </el-new-card>
    </el-col>
  </el-row>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'

export default {
  name: 'today-data',
  components: { ElNewCard },
  props: {
    gutter: Number,
    /**
     * 本日/本月数据比较
     * data的数据结构
     * data = [
     *   {
     *     name: String, // 标题
     *     value: Number // 今日值
     *     lastVal: Number // 昨日值
     *     diff: Number // 变化百分比
     *   }
     * ]
     */
    data: {
      type: Array,
      default: () => []
    }
  },
  filters: {
    lastText (val) {
      if (val.indexOf('本日') >= 0) return '昨日'
      if (val.indexOf('本月') >= 0) return '前月'
      return ''
    }
  }
}
</script>

<style lang="scss" scoped>
.comparative-data {
  margin: -10px 0;
  strong {
    font-size: 18px;
    font-weight: normal;
  }
  p {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 0;
    font-size: 12px;
  }
}
</style>
