<template>
  <div class="pagination-container">
    <el-pagination
      background
      layout="prev, pager, next, sizes"
      :page-sizes="[10,20,50]"
      :pageSize="pageSize"
      :current-page="pageNum"
      :total="total"
      @size-change="onSizeChange"
      @current-change="onCurrentChange">
    </el-pagination>
  </div>
</template>

<script>
export default {
  props: {
    getData: {
      type: Function,
      required: true
    },
    type: { // 类型。 可选值：page / popup
      type: String,
      default: 'page'
    }
  },
  data () {
    return {
      pageNum: 1,
      pageSize: 10,
      total: 0
    }
  },
  watch: {
    // 在第三级菜单完成操作后返回上一页，重新获取数据，以保证数据的更新
    $route (val, oldVal) {
      if (val.matched.length <= 2 && oldVal.matched.length > 2) {
        this.getData(this.getPageList)
      } else {
        this.changePathHash('replace')
      }
    }
  },
  created () {
    const query = this.$route.query
    this.pageNum = Number(query.pageNum) || 1
    this.pageSize = Number(query.pageSize) || 10
    this.getData(this.getPageList)
    this.changePathHash('replace')
  },
  methods: {
    getPageList (apiName, data, options) {
      return new Promise((resolve, reject) => {
        this.$http[apiName](Object.assign({}, {
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }, data), options)
          .then(res => {
            this.pageNum = res.pageNum
            this.pageSize = res.pageSize
            this.total = res.total
            resolve(res)
          })
          .catch(reject)
      })
    },

    onReset () {
      this.onSizeChange(this.pageSize)
    },

    onSizeChange (pageSize) {
      this.pageNum = 1
      this.pageSize = pageSize
      this.getData(this.getPageList)
      this.changePathHash()
    },

    onCurrentChange (pageNum) {
      this.pageNum = pageNum
      this.getData(this.getPageList)
      this.changePathHash()
    },

    // 切换页面时更新hash路径
    changePathHash (type = 'push') {
      // 弹窗不用更新hash
      if (this.type === 'popup') return

      // 避免重复跳转
      const query = this.$route.query
      if (this.pageNum === Number(query.pageNum) && this.pageSize === Number(query.pageSize)) return

      this.$router[type]({
        path: this.$route.path,
        query: {
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.pagination-container {
  padding: 30px 0;
  text-align: right;

  .el-pagination {
    padding: 0;
    font-weight: normal;

    ::v-deep .el-pagination__sizes {
      margin-right: -5px;
    }
  }
}
</style>
