<template>
  <div class="date-range">
    <el-radio-group v-model="range" @change="onChange">
      <el-radio-button label="all">全部</el-radio-button>
      <el-radio-button label="year">年</el-radio-button>
      <el-radio-button label="month">月</el-radio-button>
      <el-radio-button label="week">周</el-radio-button>
      <el-radio-button label="day">日</el-radio-button>
    </el-radio-group>
    <el-button class="date-range-custom-btn" :type="customVal.length > 0 ? 'primary' : 'default'" icon="el-icon-date" @click="showDatePicker"></el-button>

    <el-new-date-picker
      v-model="customVal"
      popper-class="date-range-custom-arrow"
      class="date-range-custom"
      ref="datePicker" @change="onChange"></el-new-date-picker>
  </div>
</template>

<script>
import ElNewDatePicker from '@/components/ElNewDatePicker'
import moment from 'moment'

export default {
  name: 'date-range',
  components: { ElNewDatePicker },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      range: 'all',
      rangeVal: [],
      customVal: []
    }
  },
  methods: {
    onChange (val) {
      if (Object.prototype.toString.call(val) !== '[object Array]') {
        const todayStart = moment().startOf('day').toDate()

        if (val === 'all') {
          this.rangeVal = []
        } else if (val === 'year' || val === 'month' || val === 'week') {
          this.rangeVal = [moment().subtract(1, 'day').subtract(1, val).startOf('day').toDate(), todayStart]
        } else if (val === 'day') {
          this.rangeVal = [todayStart, moment().toDate()]
        } else {
          return
        }

        this.customVal = []
        this.$emit('change', this.rangeVal)
      } else if (val.length > 0) {
        this.range = ''
        this.$emit('change', this.customVal)
      }
    },

    showDatePicker () {
      this.$refs.datePicker.focus()
    }
  }
}
</script>

<style lang="scss" scoped>
.date-range {
  position: relative;

  .date-range-custom-btn {
    margin-left: 10px;
    padding-top: 7px;
    padding-bottom: 7px;
    font-size: 16px;
    vertical-align: middle;
  }

  .date-range-custom {
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
  }
}
</style>

<style lang="scss">
.date-range-custom-arrow .popper__arrow {
  right: 15px!important;
  left: auto!important;
}
</style>
