<template>
  <div class="chart-container">
    <h5 class="chart-title">
      环比统计
      <el-popover :content="$slots.default ? $slots.default[0].text : '统计本账户内，当前筛选条件下已发行的积分兑换各部门商品的环比情况'">
        <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
      </el-popover>
    </h5>

    <div class="chart-main">
      <div class="chart">
        <v-chart :options="options" ref="chart"></v-chart>
      </div>
      <el-row style="margin-top: 10px">
        <el-col :span="17">
          <el-table class="table color2" :data="datas.table[0]" size="mini" style="width: 100%">
            <el-table-column prop="num" label="数量">
              <template slot-scope="scope">{{ scope.row.num | fmtMoney }}</template>
            </el-table-column>
            <el-table-column prop="proportion" label="全部商品中占比">
              <template slot-scope="scope">{{ scope.row.proportion | fmtFloat2Percent }}</template>
            </el-table-column>
            <el-table-column prop="times" label="总兑换次数"></el-table-column>
            <el-table-column prop="rate" label="兑换频率（次/日）"></el-table-column>
            <el-table-column prop="verifiedIntegral" label="核销积分数">
              <template slot-scope="scope">{{ scope.row.verifiedIntegral | fmtMoney }}</template>
            </el-table-column>
            <el-table-column prop="verifiIntegralDaily" label="日均核销积分数">
              <template slot-scope="scope">{{ scope.row.verifiIntegralDaily | fmtMoney }}</template>
            </el-table-column>
          </el-table>
        </el-col>
        <el-col :span="6" :offset="1">
          <el-table class="table color1" :data="datas.table[1]" size="mini" style="width: 100%">
            <el-table-column prop="growth" label="本期增长">
              <template slot-scope="scope">
                <span>{{ scope.row.growth | fmtFloat2Percent }}</span><arrow-icon :value="scope.row.growth" />
              </template>
            </el-table-column>
            <el-table-column prop="yearGrowth" label="同比增长">
              <template slot-scope="scope">
                <span>{{ scope.row.yearGrowth | fmtFloat2Percent }}</span><arrow-icon :value="scope.row.yearGrowth" />
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import bar from './common/bar'
import { fmtMoney, fmtFloat2Percent } from '@/filters'

export default {
  name: 'user-goods-chain',
  mixins: [bar],
  data () {
    return {
      options: {
        tooltip: {
          formatter: (params) => {
            if (params.componentType !== 'series') return ''
            const series = this.datas.chart.find(c => c.name === params.seriesName)
            const data = series.data.find(d => d.section === params.name)
            if (series.name === '数量') {
              return `${series.name}<br/>数量：${fmtFloat2Percent(data.num)}<br/>积分：${fmtFloat2Percent(data.integral)}<br/>费用（元）：${fmtFloat2Percent(data.fee)}<br/>兑换次数：${data.times}<br/>兑换频率（次/日）：${data.rate}<br/>核销积分：${fmtFloat2Percent(data.verifiIntegral)}<br/>日均核销积分：${fmtFloat2Percent(data.verifiIntegralDaily)}`
            } else {
              return `${series.name}<br/>增长率：${fmtFloat2Percent(data.ratio)}`
            }
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '商品数',
            min: 0,
            splitLine: false,
            axisLabel: { formatter: value => fmtMoney(value) }
          },
          {
            type: 'value',
            name: '增长率（%）',
            min: 0,
            splitLine: false,
            axisLabel: { formatter: value => fmtFloat2Percent(value) }
          }
        ],
        series: [
          {
            type: 'line',
            yAxisIndex: 1,
            markPoint: {
              data: [
                { type: 'max', name: '最大值' }
              ],
              label: {
                formatter: params => fmtFloat2Percent(params.value)
              }
            }
          },
          {
            type: 'bar',
            markPoint: {
              data: [
                { type: 'max', name: '最大值' },
                { type: 'min', name: '最小值' }
              ]
            },
            markLine: {
              data: [
                { type: 'average', name: '平均值' }
              ]
            }
          }
        ]
      },
      chartValue: {
        增长率: 'ratio',
        用户数: 'num'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import './common/bar.scss';

.chart-container {
  .table {
    ::v-deep .el-table__header th { padding-bottom: 0; }
    ::v-deep .el-table__body td { font-size: 16px; }
  }

  .chart-main {
     .table.color1 ::v-deep .el-table__body td { border-bottom: 6px solid #c23531; }
     .table.color2 ::v-deep .el-table__body td { border-bottom: 6px solid #2f4554; }
     .table.color3 ::v-deep .el-table__body td { border-bottom: 6px solid #61a0a8; }
     .table.color4 ::v-deep .el-table__body td { border-bottom: 6px solid #d48265; }
  }
}
</style>
